:root {
  --pink: rgb(128, 105, 214); /*hotpink*/
  --lightpink: rgb(104, 101, 207); /*#7b2f55; */
}

html {
  font-family: sans-serif;
  line-height: 20px;
}

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 1.42857143;
  background-color: var(--pink);
}

.navbar-dark .navbar-nav .active > .nav-link,
.navbar-dark .navbar-nav .nav-link.active,
.navbar-dark .navbar-nav .nav-link.show,
.navbar-dark .navbar-nav .show > .nav-link {
  color: white;
  padding: 1rem;
  letter-spacing: 0.3rem;
  padding: 10px 25px;
  font-family: "Catamaran", sans-serif;
  font-size: 1.3rem;
  font-weight: lighter;
}

.nav-link:hover {
  color: var(--pink) !important;
}

.custom-toggler .navbar-toggler-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(104,101,207, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

.custom-toggler.navbar-toggler {
  border-color: var(--pink);
}

header {
  border: 1px solid black;
  background-color: black;
  height: 100vh;
  overflow: auto;
}

.janvi {
  margin: 30vh auto;
  text-align: center;
}

.name {
  font-family: "Text Me One", sans-serif;
  color: var(--pink);
  font-size: 12rem;
}

.fed {
  font-family: "Catamaran", sans-serif;
  letter-spacing: 0.5rem;
  color: var(--lightpink);
}

@media (max-width: 767px) {
  .name {
    font-size: 4rem;
  }
}

.about {
  background-color: var(--pink);
  height: 100vh;
  overflow: auto;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.about > h1,
#projects > h1 {
  font-family: "Text Me One", sans-serif;
  margin: 10px 0px;
}

.about > img {
  border-radius: 45%;
  border: 0.5rem solid white;
}

.introduction {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 16px;
  width: 80%;
  font-weight: 100;
  letter-spacing: 0.2rem;
}

.Icode {
  font-family: "La Belle Aurore", cursive;
  font-size: 5rem;
}

#skills > p {
  width: 80%;
  text-align: center;
  margin: 0 auto;
  font-size: 1.5rem;
  font-weight: 100;
  letter-spacing: 0.2rem;
  margin-top: 10px;
}

.icons {
  width: 60%;
  display: flex;
  justify-content: center;
  color: var(--pink);
  list-style: none;
}

.icons > li {
  margin: 40px;
  font-size: 24px;
  font-weight: 500;
}

.icons > li > h3 {
  letter-spacing: normal;
}

#contact,
#skills {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  height: 100vh;
  overflow: auto;
  background-color: black;
  color: white;
}

#contact > h1,
#skills > h1 {
  font-family: "Text Me One", sans-serif;
  color: var(--pink);
}

#contact > h4,
h3 {
  font-family: "Text Me One", sans-serif;
  margin: 10px 0;
  letter-spacing: 0.3rem;
}

#links {
  display: flex;
  list-style: none;
}

#links > li {
  margin: 10px 20px;
}

#projects {
  background-color: var(--pink);
  color: white;
  text-align: center;
  height: 100vh;
}

#projects > h1 {
  padding: 2rem 0;
  font-size: 36px;
}

.project-content {
  width: 60%;
  align-items: center;
}

.project-title {
  font-family: "Catamaran", sans-serif;
  letter-spacing: 0.3rem;
  font-size: 2rem;
  font-weight: 100;
  margin-bottom: 2em;
  text-align: left;
}

.project-suite {
  margin-top: -40px;
}

.project-suite > span {
  color: #212121;
  font-family: "Catamaran", sans-serif;
  font-size: 1.5em;
  margin: 10px;
}

.project-description {
  text-align: left;
  margin-top: 0.9em;
  font-family: "Text Me One", sans-serif;
  font-size: 1.3em;
  font-weight: 100;
  letter-spacing: 0.2rem;
}
